
export default {
  data() {
    return {
      inputQuery: "",
      errorInput: false,
    };
  },
  methods: {
    checkForm() {
      !this.inputQuery.length
        ? (this.errorInput = true)
        : (this.errorInput = false);
    },
    setQuery() {
      this.$emit("getPhotosCollection", this.inputQuery);
      this.inputQuery = "";
    },
  },
};
